<template>
  <v-app-bar app color="white" dense elevation="0" shrink-on-scroll>
    <v-spacer />
    <div class="div-btn-t" id="shrink-btns">
      <v-btn
        text
        color="white"
        v-for="(item, index) in toolbar"
        :key="index"
        class="mark"
      >
        <span class="appbar-item-btn">{{ item.content }}</span>
      </v-btn>
    </div>
    <div class="custom-starry-icon-t" id="shrink-icon" v-on="handleScroll">
      <v-icon large class="custom-starry-icon-left-t">{{ starryIcon }}</v-icon>
    </div>
    <v-spacer />
  </v-app-bar>
</template>

<script>
export default {
  components: {},

  data() {
    return {
      starryIcon: "mdi-crop-free",
      toolbar: [
        { content: "About" },
        { content: "News" },
        { content: "Product" },
        { content: "Event" }
      ]
    };
  },
  methods: {
    handleScroll() {
      if (
        document.body.scrollTop > 5 ||
        document.documentElement.scrollTop > 5
      ) {
        let s = "20%";
        document.getElementById("shrink-icon").style.top = s;
        document.getElementById("shrink-btns").style.top = s;
      } else {
        let s = "35%";
        document.getElementById("shrink-icon").style.top = s;
        document.getElementById("shrink-btns").style.top = s;
      }
    }
  },
  created() {
    window.addEventListener("scroll", this.handleScroll);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  mounted() {
    this.handleScroll();
  }
};
</script>

<style>
.custom-starry-icon-t {
  position: relative;
}
.custom-starry-icon-left-t {
  position: absolute;
  transform: rotate(45deg);
  z-index: 1;
}
.div-btn-t {
  position: relative;
}
</style>
